* {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

body, h3, p {
  margin: 0;
}

.container {
  text-align: center;
  background-color: #fff;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.container__logo {
  width: 40px;
  height: 40px;
}

.nav {
  text-transform: uppercase;
}

.nav__menu {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: inherit;
  height: 60px;
  line-height: 60px;
  text-decoration: none;
  transition: all .3s;
  display: inline-block;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: relative;
  bottom: 4px;
}

.main {
  grid-template-columns: repeat(1, 200px);
  gap: 46px 48px;
  width: fit-content;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

@media (width >= 1200px) {
  .main {
    padding: 50px 128px;
  }
}

.card {
  color: #060b35;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: all .3s;
}

.card__content {
  flex-direction: column;
  display: flex;
}

.card__img {
  align-self: center;
  width: 160px;
  height: 134px;
}

.card__description {
  font-size: inherit;
  font-weight: inherit;
  margin-top: 40px;
  line-height: 18px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__description {
  color: #34568b;
}

.card__product-code {
  color: #616070;
  width: fit-content;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.card__reviews {
  align-self: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  margin-top: 24px;
}

.card__button {
  text-transform: uppercase;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: all .3s;
  display: flex;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.stars, .stars__rating {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:not(:first-child) {
  margin-left: 4px;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price__elem {
  color: #616070;
  font-weight: 400;
}

.price__item {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}
